                    width:100%;
                    height:30px;
                    background: rgba(0, 0, 0, 0.2);
                    display: flex;
                    transition: @animateTime;
                    transform: translateY(100%);
                    .item{
                      flex: 1 1 auto;
                      transition: @animateTime;
                      font-size: 14px;
                      &.delete{
                        color: @main-color;
                        border-left: 1px solid rgba(255, 255, 255, 0.2);
                      }
                      &.edit{
                        color: @main-blue;
                      }
                      &:hover{
                        background: rgba(0, 0, 0, 0.05);
                      }
                    }
                  }
                  &:hover{
                    // background: rgba(0, 0, 0, 0.05);
                    .name{
                      transform: translateY(-15px);
                    }
                    .ctrl-box{
                      transform: translateY(0%);
                    }
                  }
                  
                  &.delete{
                    color: @main-color;
                  }
                  &.edit{
                    color: @main-blue;
                  }
                  &.add{
                    color: @main-green;
                  }
                }
              }
            }
          }
        }
        .btn-box{
          width:100%;
          height: 60px;
          flex: 0 0 auto;
          background: rgba(255, 255, 255, 0.6);
        }
      }
      .bg{
        position: absolute;
        z-index: 1;
        top:0;
        left:0;
        margin-left: -50%;
        margin-top: -50%;
        width: 200%;
        height: 200%;
        background-attachment: fixed;
        background-repeat: no-repeat;
        background-position: center;
        background-color: rgb(255, 255, 255);
        background-size: 100%;
        filter: blur(30px);
      }
    }
  }

  .bookmark-edit-add{
    position: fixed;
    top:0;
    left:0;
    z-index: 1000;
    width:100%;
    height:100%;
    .bm-ea-box{
      position: absolute;
      left:50%;
      margin-left: -200px;
      top:50%;
      margin-top: -250px;
      width:400px;
      height:500px;
      box-shadow: @box-shadow;
      overflow: hidden;
      .bm-ea-content{
        position: absolute;
        z-index: 2;
        top:0;
        left:0;
        width:100%;
        height:100%;
        background:rgba(255, 255, 255, .7);
        .preview-box{
          width: 100%;
          height: 120px;
          margin-top: 20px;
        }
        .bm-ea-set-detail{
          padding: 0 50px;
          .bm-ea-detail-box{
            font-weight: bold;
            height:40px;
            display: flex;
            align-items: center;
            .name{
              font-size: 20px;
              color: @main-txt-color;
              font-weight: bold;
              user-select: none;
            }
            .input{
              flex-grow: 1;
              margin-left: 8px;;
              width: 200px;
              font-size: 20px;
              border-top: none;
              border-left: none;
              border-right: none;
              background:transparent;
              transition: @animateTime;
              &[type="text"]{
                color: @main-txt-color;
                border-bottom: 1px solid rgba(255, 255, 255, 0.6);
              }
              &:focus{
                outline: none;
                color: #000;
                border-color: @main-color;
              }
              .color-item{
                width: 20px;
                height: 20px;
                float: left;
                margin: 2px;
                background: red;
                cursor: pointer;
                transition: @animateTime;
                &.active{
                  transform: scale(.8);
                  &::after{
                    content: "";
                    position: absolute;
                    width: 20px;
                    height: 20px;
                    transform: scale(1.25);
                    border: 1px solid #de1a1a;
                  }
                }
              }
            }
          }
          .icontype-box{
            position: absolute; 
            width:300px;
            height: 150px;
            // border: 1px solid rgba(255, 255, 255, 0.8);
            .image-box{
              position: relative;
              width: 120px;
              height:120px;
              border: 1px solid rgba(0, 0, 0, 0.2);
              cursor: pointer;
              overflow: hidden;
              border-radius: 50%;
              >img{
                width: 100%;
              }
              .input-img{
                position: absolute;
                z-index: 10;
                top:0;
                left:0;
                width:100%;
                height:100%;
                // border-radius: 50%;
                transition: @animateTime;
                background: rgba(0, 0, 0, 0.3);
                font-size: 32px;
                color:#fff;
                opacity: 0;
                &:hover{
                  opacity: 1;
                }
              }
            }
          }
          .txt-type-box{
            position: absolute;
          }
        }
        .bottom-ctrl{
          position:absolute;
          bottom:0;
          width:100%;
          height:60px;
          background:rgba(255, 255, 255, 0.6);
        }
      }
      .bm-ea-bg{
        position: absolute;
        z-index: 1;
        left: -50vw;
        top:-10vh;
        width:100vw;
        height:100vh;
        background-attachment: fixed;
        background-repeat: no-repeat;
        background-position: 0 0;
        background-size: 100%;
        filter: blur(30px);
      }
    }
  }
}
</style>



